@import url(https://fonts.googleapis.com/css2?family=Gloria+Hallelujah&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  font-family: 'Gloria Hallelujah', cursive;
}

.App {
  text-align: center;
}

.CanvasContainer{
  margin-top: 20vh;
  text-align: center;
}
.canvas {
  background-color: rgb(240, 240, 240);
  border-radius: 10px;
  touch-action:none
}

.BigInfo{
  text-align: center;
  margin-top: 40vh;
}


@-webkit-keyframes changeLetter {
  0% {
    content: "z";
  }
  50% {
    color: white;
  }
  100% {
    content: "zZ";
  }
}


@keyframes changeLetter {
  0% {
    content: "z";
  }
  50% {
    color: white;
  }
  100% {
    content: "zZ";
  }
}

.letter-changer {
  content: "z";
  -webkit-animation: changeLetter 3s linear infinite alternate;
          animation: changeLetter 3s linear infinite alternate;
}
.ButtonsContainer{
    margin-top: 10px;
    
    font-family: sans-serif;
    display: flex;
    text-align: center;
    width: 180px;
    margin-right: auto;
    margin-left: auto;
}

.button {
    font-family: Arial;
    font-size: 12px;
    padding: 8px 30px;
    text-transform: uppercase;
    text-align: center;
    border: 0;
    border-radius: 3px 3px 0 0px;
    cursor: pointer;
    margin-right: 5px;
  }
  
  .button.green {
    background-color: #2c8b2c;
    border-bottom: 12px solid #185d00;
    color: #FFF;
  }
  
  .button.yellow {
    background-color: #f6f15c;
    border-bottom: 12px solid #dbe300;
    color: #444;
  }
  
  .button.red {
    background-color: #e75959;
    border-bottom: 12px solid #e72626;
    color: #FFF;
  }
  
  .button.white {
    background-color: #ffffff;
    border: 1px solid #00000027;
    border-bottom: 12px solid #00000027;
    color: rgb(3, 3, 3);
  }
  

  .button:hover {
    opacity: .9;  
  }
  
  .button:active {
    transform: translatey(12px);
    border: 0;
    margin-bottom: 12px;
  }

  button:focus {outline:0;}
.InfoWeb {
    position: fixed;
    top: 5vh;

    width: 100vw;
    text-align: center;
    color: rgba(0, 0, 0, 0.7);
}

.InfoWebHead {
    width: 300px;
    margin-left: auto;
	margin-right: auto;
    -webkit-animation: grow 2000ms ease infinite;
            animation: grow 2000ms ease infinite;
    border-radius: 5px;
    line-height: 170%;
    padding: 10px;
}

@-webkit-keyframes grow {
	0% {
		transform: scale(1);
	}

	14% {
		transform: scale(1.2);
	}

	28% {
		transform: scale(1);
	}

	42% {
		transform: scale(1.2);
	}

	70% {
		transform: scale(1);
	}
}

@keyframes grow {
	0% {
		transform: scale(1);
	}

	14% {
		transform: scale(1.2);
	}

	28% {
		transform: scale(1);
	}

	42% {
		transform: scale(1.2);
	}

	70% {
		transform: scale(1);
	}
}
